import React, { useState } from "react";
import axios from "axios";
import { useMutation } from "@tanstack/react-query";

import {
  Container,
  Logo,
  Text,
  InputContent,
  InputBox,
  InputText,
  Input,
  Button,
  WarningText,
  Content,
} from "./styles";

import { useNavigate } from "react-router-dom";

/**
 *    
 * @author 
 */
const TempPassword = () => {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [isValid, setIsValid] = useState(false);
  const [warningMessage, setWarningMessage] = useState("");

  /**
   *     
   * @param {string} value 
   */
  const emailCheck = (value: string) => {
    const regExp =
      /^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/;
    if (!regExp.test(value)) {
      setIsValid(false);
      setWarningMessage("   .");
    } else {
      setIsValid(true);
      setWarningMessage("");
    }
  };

  /**
   *     api 
   * @param {string} email 
   */
  const getTempPassword = async () => {
    try {
      const { data } = await axios.post(
        `${process.env.REACT_APP_API}/temp-password`,
        { email: email }
      );

      if (data) {
        alert(data);
        navigate("/login");
      }
    } catch (error: any) {
      alert("  .");
    }
  };

  const { mutate: getTempPasswordMutate } = useMutation({
    mutationFn: getTempPassword,
  });

  return (
    <>
      <Container>
        <Content>
          <Logo>NEWSERIAL</Logo>
          <Text>
             
            <br />   .
            <br />    .
          </Text>
          <InputContent>
            <InputBox>
              <InputText></InputText>
              <Input
                name="email"
                value={email}
                onChange={(e) => {
                  setEmail(e.target.value);
                  emailCheck(e.target.value);
                }}
              />
              {email !== "" && <WarningText>{warningMessage}</WarningText>}
            </InputBox>
          </InputContent>
          <Button disabled={!isValid} onClick={() => getTempPasswordMutate()}>
            
          </Button>
        </Content>
      </Container>
    </>
  );
};

export default TempPassword;
