import React, { useEffect } from "react";
import { useNavigate } from "react-router";
import { useDispatch } from "react-redux";
import { setToken } from "../../redux/modules/auth";
import { useState } from "react";
import axios from "axios";
import {
  Container,
  Logo,
  InputContent,
  InputBox,
  InputText,
  Input,
  Button,
  BottomText,
  Content,
  ColorText,
} from "./styles";
import { useMutation } from "@tanstack/react-query";

/**
 *  
 * @author 
 */
const Login = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [inputs, setInputs] = useState({ email: "", password: "" });
  const { email, password } = inputs;

  /**
   *  
   * @param {string} email 
   * @param {string} password 
   */
  const login = async () => {
    try {
      const { data } = await axios.post(
        `${process.env.REACT_APP_API}/login`,
        {
          email: email,
          password: password,
        },
        {
          withCredentials: true,
        }
      );
      dispatch(setToken(data.accessToken));
      navigate("/");
    } catch (error: any) {
      alert("   .");
    }
  };

  // 
  const naverLogin = async () => {
    const url = `${process.env.REACT_APP_API}/oauth2/authorization/naver`;
    try {
      window.location.href = new URL(url, window.location.origin).href;
    } catch (error: any) {
      console.log(error);
    }
  };

  const { mutate: loginMutate } = useMutation({
    mutationFn: login,
  });
  const { mutate: naverLoginMutate } = useMutation({
    mutationFn: naverLogin,
  });

  /**
   * inputs  
   * @param {React.ChangeEvent<HTMLInputElement>} e 
   */
  const changeInput = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setInputs({ ...inputs, [name]: value });
  };

  return (
    <Container>
      <Content>
        <Logo>NEWSERIAL</Logo>
        <InputContent>
          <InputBox>
            <InputText></InputText>
            <Input
              name="email"
              value={email}
              onChange={(e) => changeInput(e)}
            />
          </InputBox>
          <InputBox>
            <InputText></InputText>
            <Input
              type="password"
              name="password"
              value={password}
              onChange={(e) => changeInput(e)}
            />
          </InputBox>
        </InputContent>
        <ColorText onClick={() => navigate("/temp-password")}>
           ?
        </ColorText>
        <Button onClick={() => loginMutate()}></Button>
        <BottomText>
          Newserial  ? &nbsp;
          <span onClick={() => navigate("/signup")}> </span>
        </BottomText>
        <img
          src="/assets/images/btnD_.png"
          width={150}
          onClick={() => naverLoginMutate()}
        />
      </Content>
    </Container>
  );
};

export default Login;
