import React, { useState } from "react";
import axios from "axios";
import { useMutation } from "@tanstack/react-query";

import {
  Container,
  Logo,
  InputContent,
  InputBox,
  InputText,
  Input,
  Button,
  ModalText,
  ModalInput,
  ModalButton,
  WarningText,
  Content,
} from "./styles";

import Modal from "../../components/Modal/index";
import { useNavigate } from "react-router-dom";

const Signup = () => {
  const navigate = useNavigate();
  const [toggle, setToggle] = useState(false);
  const [inputs, setInputs] = useState({
    email: "",
    code: "",
    password: "",
    checkPassword: "",
  });
  const [isValid, setIsValid] = useState({
    emailFormat: false,
    emailDuplication: false,
    emailCode: false,
    passwordFormat: false,
    passwordCheck: false,
  });
  const [warningMessage, setWarningMessage] = useState({
    email: "",
    password: "   .",
    checkPassword: "  .",
    code: "",
  });
  const { email, code, password, checkPassword } = inputs;

  const submitRequirements =
    isValid.emailFormat &&
    isValid.emailCode &&
    isValid.passwordFormat &&
    isValid.passwordCheck;

  /**
   *    
   * @author 
   * @param {string} type input name
   * @param {string} value input value
   *
   * @return {boolean}  true  false
   */
  const inputCheck = (type: string, value: string) => {
    if (type === "email") {
      const regExp =
        /^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/;
      if (!regExp.test(value)) {
        setIsValid({ ...isValid, emailFormat: false });
        setWarningMessage({
          ...warningMessage,
          email: "   .",
        });
      } else {
        setIsValid({ ...isValid, emailFormat: true, emailCode: false });
        setWarningMessage({
          ...warningMessage,
          email: "",
        });
      }
    } else if (type === "password") {
      const regExp = /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,20}$/;
      if (!regExp.test(value)) {
        setIsValid({
          ...isValid,
          passwordFormat: false,
        });
      } else {
        if (checkPassword !== value)
          setIsValid({
            ...isValid,
            passwordFormat: true,
            passwordCheck: false,
          });
        else setIsValid({ ...isValid, passwordCheck: true });
      }
    } else if (type === "checkPassword") {
      if (password !== value) setIsValid({ ...isValid, passwordCheck: false });
      else setIsValid({ ...isValid, passwordCheck: true });
    }
  };

  /**
   * inputs  
   * @author 
   * @param {React.ChangeEvent<HTMLInputElement>} e 
   */
  const changeInput = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setInputs({ ...inputs, [name]: value });
    inputCheck(name, value);
  };

  /**
   *   api  (   )
   * @author 
   * @param {string} email 
   */
  const sendMail = async () => {
    setInputs({ ...inputs, code: "" });
    const { data } = await axios.post(`${process.env.REACT_APP_API}/email`, {
      email: email,
    });

    if (data === "    .") {
      setWarningMessage({ ...warningMessage, code: "", email: `${data}` });
    } else {
      setWarningMessage({ ...warningMessage, code: "" });
      setToggle(true);
    }
  };

  /**
   *   
   * @author 
   */
  const checkNumber = async () => {
    const { data } = await axios.post(
      `${process.env.REACT_APP_API}/email-number`,
      {
        email: email,
        code: code,
      }
    );
    if (data === ".") {
      setIsValid({ ...isValid, emailCode: true });
      setToggle(false);
    } else {
      setIsValid({ ...isValid, emailCode: false });
      setWarningMessage({
        ...warningMessage,
        code: "  .",
      });
    }
  };

  /**
   *  api  
   * @author 
   * @param {string} email 
   * @param {string} password 
   */
  const signup = async () => {
    const { data } = await axios.post(`${process.env.REACT_APP_API}/members`, {
      email: email,
      password: password,
    });

    if (data.message === "  ") {
      alert(`${data.message}`);
      navigate("/login");
    } else {
      setWarningMessage({ ...warningMessage, email: `${data.message}` });
      setIsValid({ ...isValid, emailFormat: false });
    }
  };

  const { mutate: sendMailMutate } = useMutation({
    mutationFn: sendMail,
  });

  const { mutate: checkNumberMutate } = useMutation({
    mutationFn: checkNumber,
  });

  const { mutate: signupMutate } = useMutation({
    mutationFn: signup,
  });

  return (
    <>
      {toggle && (
        <Modal
          setToggle={setToggle}
          content={
            <>
              <ModalText>
                    .
              </ModalText>
              <ModalInput
                name="code"
                value={code}
                onChange={(e) => changeInput(e)}
              />
              {!isValid.emailCode && (
                <WarningText>{warningMessage.code}</WarningText>
              )}
              <ModalButton onClick={() => checkNumberMutate()}>
                
              </ModalButton>
            </>
          }
        />
      )}
      <Container>
        <Content>
          <Logo>NEWSERIAL</Logo>
          <InputContent>
            <InputBox>
              <InputText></InputText>
              <Input
                name="email"
                value={email}
                onChange={(e) => changeInput(e)}
              />
              <button
                className="input-box__button"
                onClick={() => sendMailMutate()}
                disabled={!isValid.emailFormat}
              >
                
              </button>
              {email !== "" && (
                <WarningText>{warningMessage.email}</WarningText>
              )}
            </InputBox>
            <InputBox>
              <InputText></InputText>
              <Input
                type="password"
                name="password"
                value={password}
                placeholder=", ,   8~20"
                onChange={(e) => changeInput(e)}
              />
              {password !== "" && !isValid.passwordFormat && (
                <WarningText>{warningMessage.password}</WarningText>
              )}
            </InputBox>
            <InputBox>
              <InputText> </InputText>
              <Input
                type="password"
                name="checkPassword"
                value={checkPassword}
                onChange={(e) => changeInput(e)}
              />
              {checkPassword !== "" && !isValid.passwordCheck && (
                <WarningText>{warningMessage.checkPassword}</WarningText>
              )}
            </InputBox>
          </InputContent>
          <Button disabled={!submitRequirements} onClick={() => signupMutate()}>
            
          </Button>
        </Content>
      </Container>
    </>
  );
};

export default Signup;
