import React, { useEffect, useState } from "react";
import { Outlet, Navigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import api from "../api";
import { setToken } from "../redux/modules/auth";
import { useQuery } from "@tanstack/react-query";

interface RootState {
  auth: {
    accessToken: null | string;
  };
}

/**
 *      
 * @author 
 */
const PublicRoute = () => {
  const dispatch = useDispatch();
  const accessToken = useSelector((state: RootState) => state.auth.accessToken);

  /**
   * refresh Token  accessToken  api 
   */
  const refreshLogin = async () => {
    const { data } = await api.get(`/reissue`, {
      withCredentials: true,
    });

    if (data) dispatch(setToken(data.accessToken));
  };

  const { isLoading } = useQuery({
    queryKey: ["refresh-login"],
    queryFn: refreshLogin,
    enabled: accessToken === null || accessToken==='',
  });


  if (accessToken !== null && accessToken!=='') return <Navigate to={"/"} />;
  if (isLoading) return null;

  return accessToken!==null && accessToken!==''? <Navigate to={"/"} /> : <Outlet />;
};

export default PublicRoute;
