import React, { ReactNode } from "react";
import { Container, Overlay, Box } from "./styles";

/**
 * Modal 
 * @author 
 * @param {React.Dispatch<React.SetStateAction<boolean>>} setToggle     
 * @param {ReactNode} content  
 */

interface Modal {
  setToggle: React.Dispatch<React.SetStateAction<boolean>>;
  content: ReactNode;
}

const Modal = ({ setToggle, content }: Modal) => {
  return (
    <Container>
      <Overlay onClick={() => setToggle(false)}>
        <Box onClick={(e) => e.stopPropagation()}>
          <img
            src="/assets/icons/icon_cancel.svg"
            onClick={() => setToggle(false)}
          />
          {content}
        </Box>
      </Overlay>
    </Container>
  );
};

export default Modal;
